<template>
  <div class="renli">
    <div>
      <el-button-group>
        <el-button type="primary" @click="pass()">
          审核通过
          <i class="iconfont icon-duihao"></i>
        </el-button>
        <el-button type="primary" @click="nopass()">
          审核不通过
          <i class="iconfont icon-cuohao03"></i>
        </el-button>
      </el-button-group>
    </div>
    <div class="table">
          <el-table
            ref="multipleTable"
            :data="tableData"
            stripe
            border
            tooltip-effect="dark"
            style="width: 100%"
            :cell-style="cellHeight"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="序号" width="120">
              <template slot-scope="scope">{{ scope.row.number }}</template>
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="120"></el-table-column>
            <el-table-column prop="sex" label="性别" show-overflow-tooltip></el-table-column>
            <el-table-column prop="jobNumber" label="工号" show-overflow-tooltip></el-table-column>
            <el-table-column prop="joinDate" label="入职时间" show-overflow-tooltip></el-table-column>
            <el-table-column prop="seniority" label="工龄" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">撤回</el-button>
              </template>
            </el-table-column> -->
          </el-table>
    </div>
   
    <el-pagination
        background
        layout="prev, pager, next, sizes, total, jumper"
        :page-sizes="[10, 15, 20, 25]"
        :page-size="10"
        :total="this.total"
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
      ></el-pagination>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      msg: "人力资源公司审核测试",
      table: [],
      tableData: [], // 把人员拿出来放在tableDate数组中
      ids: [] ,  // 选中的ids数组
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10 ,// 一次请求多少
    };
  },
  created() {
    console.log("进入人力资源11111111111111")
    console.log(sessionStorage.getItem("token"))
  },
  mounted(){
     console.log("进入人力资源")
    console.log(sessionStorage.getItem("token"))
    this.check();
    console.log(this.tableData);
  },
  methods: {
     cellHeight() {
      return "padding:5px 0 !important";
    },
    //   选中获取ids
    handleSelectionChange(val) {
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },

      // 请求可审核的单据
  check(){
    this.tableData = [];
       this.$axios
      .get("/audit/list?limit=" + this.pageNum + "&page=" + this.pageSize)
      .then(res => {
        console.log("renli", res);
        this.total = res.data.total;           
        this.table = res.data.data;
        for (var j = 0, len = this.table.length; j < len; j++) {
          console.log(this.table[j].employee);
          const employee = this.table[j].employee;
          employee.number = j;
          employee.id = this.table[j].id;
          if (employee.sex == "0") {
            employee.sex = "男";
          } else {
            employee.sex = "女";
          }
          this.tableData.push(this.table[j].employee);
          // console.log(this.tableData);
        }
      })
      .catch(erro => {
        console.log(erro);
      });
  },
  
  // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.check();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.check();
    },


    //   撤回
    // handleDelete(row) {
    //   console.log(row);
    //   this.$axios
    //     .put("/audit/back/" + row.id)
    //     .then(res => {
    //       console.log("撤回单据", res);
    //       // location.href = location.href;
    //       this.check();
    //     })
    //     .catch(erro => {
    //       console.log(erro);
    //     });
    // },

    //   审核通过
    pass() {
      this.$axios
        .put(
          "/audit/Y",
          qs.stringify(
            {
              auditIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        )
        .then(res => {
          console.log("审核通过", res);
          // location.href = location.href;
          this.check();
        })
        .catch(erro => {
          console.log(erro);
        });
    },
    //   审核不通过
    nopass() {
        this.$axios
        .put(
          "/audit/N",
          qs.stringify(
            {
              auditIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        ) 
        .then(res => {
          console.log("审核不通过", res);
          location.href = location.href;
        })
        .catch(erro => {
          console.log(erro);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.renli {
  .table {
    margin-top: 5px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  }
}
// .el-table td{
//   padding: 0 !important;
// }
// .el-table__row {
//   td {
//     padding: 0 !important;padding: 0 !important;
//   }
// }
// .el-table_1_column_2   {
//  padding:6px  0 !important;
// }
.el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 40px;
}
.el-table__body tr,
  .el-table__body td {
    padding: 0;
    height: 40px;
}
</style>


