<template>
  <div class="qiquan">
    <div>
       <span style="font-size:14px;">行业:  </span> 
      <el-select @change="choose" size="mini"  v-model="select" slot="prepend" placeholder="请选择行业">
        <el-option label="配件业" value="配件业"></el-option>
        <el-option label="配件业企权干股管理中心" value="配件业企权干股管理中心"></el-option>
        <el-option label="商贸业企权干股管理中心" value="商贸业企权干股管理中心"></el-option>
        <el-option  label="成品业机电贸易业企权干股管理中心" value="成品业机电贸易业企权干股管理中心"></el-option>
      </el-select>

      <el-table 
        :data="list" 
        border 
        stripe
        highlight-current-row
        :cell-style="cellHeight"
        class="scrollbar"
        style="width: 100%"
        >
        <el-table-column  fixed prop="employee.name" label="姓名"></el-table-column>
        <el-table-column prop="approvalCode" width="110px" label="批复文号"></el-table-column>
        <el-table-column prop="equityPeriod" width="130px" label="企权期间"></el-table-column>
        <el-table-column prop="approvalMoney" width="150px" label="批复可分配的总金额"></el-table-column>
        <el-table-column prop="employee.sex" label="性别"></el-table-column>
        <el-table-column prop="employee.company.name" label="公司"></el-table-column>
        <el-table-column prop="employee.idCard" width="170px" label="身份证号码"></el-table-column>
        <el-table-column prop="employee.postJob" width="60px" label="岗位"></el-table-column>
        <el-table-column prop="employee.postArea" width="100px" label="人员范围"></el-table-column>
        <el-table-column prop="cashDate" label="兑现年度"></el-table-column>
        <el-table-column prop="shareMoney" width="140px" label="可受企权的总金额"></el-table-column>
        <el-table-column prop="employee.joinDate" width="150px" label="入职时间"></el-table-column>
        <el-table-column prop="employee.seniority" width="50px" label="工龄"></el-table-column>
        <el-table-column prop="auditSts" fixed="right" width="100px" label="状态"></el-table-column>
        <!-- <el-table-column fixed="right" width="140px" label="操作">
          <template slot-scope="scope">
            <el-button type="text" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            <el-button type="text" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            <el-button type="text" @click="handlesubmit(scope.$index, scope.row)">提交</el-button>
          </template>
        </el-table-column> -->
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next, sizes, total, jumper"
        :page-sizes="[6, 10, 15, 20]"
        :page-size="10"
        :total="this.total"
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
      ></el-pagination>
      <!-- <el-row>
        <el-col :span="2">
          <div style="width:100px;" class="grid-content bg-purple">
            <el-row class="handle">
              <el-button type="primary" @click="add()">增加</el-button>
            </el-row>
          </div>
        </el-col>

        <el-col :span="22">
          <div class="grid-content bg-purple-light">
            <div class="el-botton">
              <el-upload
                class="upload-demo"
                :multiple="false"
                :auto-upload="true"
                list-type="xls || xlsx"
                :show-file-list="true"
                action
                :limit="1"
                :on-exceed="handleExceed"
                :http-request="uploadFile"
              >
                <el-button size="small" type="primary">点击导入</el-button>
                <div class="el-upload__tip" slot="tip">一次只能上传一个文件，请上传excell文件，单文件不超过1MB</div>
              </el-upload>
            </div>
          </div>
        </el-col>
      </el-row> -->
    </div>
  </div>
</template>

<script>


// import axios from "axios";
export default {
  data() {
    return {
      list: [],
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10 ,// 一次请求多少
      select:'配件业',
    };
  },
  created() {
    this.getlist();
  },
  methods: {
    cellHeight(){
       return "padding:2px 0 !important"
    },
     choose(){
       console.log(this.select);
       this.getlist();
    },
    getlist() {
      // this.loading = true;
      this.$axios
        .get("/basicEquity/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize,
            auditStsArray: "Y",
            industry: this.select
          }
        })
        .then(res => {
          console.log("选择aaa", res);
          // this.loading = false;
          this.total = res.data.total;
          this.list = res.data.data;
          for (var i = 0; i < this.list.length; i++) {
            if (this.list[i].employee.sex == "0") {
              this.list[i].employee.sex = "男";
            } else {
              this.list[i].employee.sex = "女";
            };
            switch (this.list[i].auditSts) {
              case "Y":
                this.list[i].auditSts = "审核通过";
                break;
              case "N":
                this.list[i].auditSts = "审核不通过";
                break;
              case "C":
                this.list[i].auditSts = "待审核";
                break;
              case "X":
                this.list[i].auditSts = "待提交";
                break;
            };
            this.list[i].shareRatio = this.list[i].shareRatio * 100 + "%";
          }
          console.log(this.list, "处理list");
          this.total = res.data.data.length;
        })
        .catch(err => {
          console.log(err);
          //   this.$alert("请求超时，刷新重试！");
        });
    },
    // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getlist();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getlist();
    },

  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
.handle {
  margin-top: 30px;
  width: 250px;
}
.upload {
  margin-left: 15px;
}
.upload-demo {
  margin-top: 30px;
}
.el-upload__tip {
  width: 200px;
  margin-left: 10px;
}
.amendmes {
  position: absolute;
  height: 500px;
  overflow: auto;
  top: 20px;
  left: 50px;
  right: 50px;
  bottom: 60px;
  z-index: 100;
  padding: 15px;
  background: white;
  border-radius: 9px;
  border: 1px solid #ebeef5;
 box-shadow: 0 2px 12px 0 rgba(0,0,0,.1)
  
}
.qiquaninput{
      background: white;
}
.el-table td, .el-table th {
  padding: 5px 0 !important;
}
</style>




